import styled from "styled-components";
import Detail from "./Detail";

const Container = styled.div`
  width: 90%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
`;

const CurrentDetails = (props) => {
  const { data } = props;
  return (
    <Container>
      <Detail name={"sunrise"} data={data} />
      <Detail name={"sunset"} data={data} />
      <Detail name={"wind"} data={data} />
      <Detail name={"pressure"} data={data} />
      <Detail name={"humidity"} data={data} />
      <Detail name={"cloudiness"} data={data} />
    </Container>
  );
};

export default CurrentDetails;
