import styled, { keyframes } from "styled-components";

import { move_in } from "../styles/Animations";

const Container = styled.div`
  animation: ${move_in("5rem")} 2s ease-in;

  height: 90%;
  /* background-color: grey; */
  width: 8rem;
  margin: 0 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  & img {
    margin-top: -2rem;
    margin-bottom: -2rem;
  }

  & p {
    margin-top: -1rem;
    font-size: 1.2rem;
    font-weight: 700;
    opacity: 0.8;

    & span {
      opacity: 0.9;
      font-weight: 300;
    }
  }

  & .weatherCondition {
    font-weight: 700;
  }
`;

const DailyForecast = (props) => {
  const { data, day, timezone } = props;

  const localTime = new Date(data.dt * 1000 + timezone * 1000);
  console.log(data);

  localTime.setDate(localTime.getDate());
  const date = localTime.toLocaleDateString("en-US", {
    day: "numeric",
    month: "long",
  });
  const weekday = localTime.toLocaleDateString("en-US", {
    weekday: "long",
  });

  return (
    <Container>
      <h2>{weekday}</h2>
      <h3>{date}</h3>
      <img
        src={`http://openweathermap.org/img/wn/${data.weather[0].icon}@2x.png`}
        alt="icon"
      />{" "}
      <p className="weatherCondition">{data.weather[0].main}</p>
      <p>
        {String(data.temp.day).slice(0, 2)}&deg;C{" "}
        <span>{String(data.temp.night).slice(0, 2)}&deg;C</span>
      </p>
    </Container>
  );
};

export default DailyForecast;
