import { keyframes } from "styled-components";

export const appear = keyframes`
from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }`;

export const move_in = (distance) => keyframes`
from {
    transform: translateY(${distance});
    opacity: 0;
  }

  to {
    transform: translateY(0);
    opacity: 1;
  }`;
