import styled from "styled-components";

import CurrentDetails from "./CurrentDetails";

import { move_in } from "../styles/Animations";
import { device } from "../res/Device";

const Container = styled.div`
  animation: ${move_in("10rem")} 2s ease-in;
  width: 80vw;
  margin: 7rem 3rem;
  display: flex;
  justify-content: space-around;

  @media ${device.laptopL} {
    animation: ${move_in("8rem")} 2s ease-in;
  }
`;

const CityNotFound = styled.div`
  animation: ${move_in("10rem")} 2s ease-in;
  width: 80vw;
  margin: 10rem 5rem;
  display: flex;
  justify-content: space-around;
  text-transform: uppercase;
  letter-spacing: 2px;
  h1 {
    font-weight: 400;
  }
  @media ${device.laptopL} {
    animation: ${move_in("8rem")} 2s ease-in;
  }
`;

const CurrentTemp = styled.div`
  width: 25rem;
  height: 20rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;

  @media ${device.laptopL} {
    width: 20rem;
    height: 16rem;
  }

  & .temperature {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
  }
  & h1 {
    @media ${device.laptopL} {
      font-size: 2.2rem;
    }
  }
  & h2 {
    font-size: 3rem;
    text-transform: capitalize;

    @media ${device.laptopL} {
      font-size: 2rem;
    }
  }

  & img {
    width: 8rem;
  }
`;

const WeatherDetails = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 35rem;
  height: 20rem;

  @media ${device.laptopL} {
    width: 33rem;
    height: 16rem;
  }

  & span {
    margin-left: 2rem;
    font-size: 1.4rem;
    font-weight: 400;
  }
`;

const CityWeather = (props) => {
  const { data } = props;
  const localTime = new Date(data.dt * 1000 + data.timezone * 1000);
  const currentDate = localTime.toLocaleDateString("en-US", {
    month: "long",
    weekday: "long",
    day: "numeric",
  });
  const hour = localTime.getHours();
  const minutes = localTime.getMinutes();
  const currentHour = `${hour < 10 ? "0" + hour : hour}: ${
    minutes < 10 ? "0" + minutes : minutes
  }`;
  return data.name ? (
    <Container>
      <CurrentTemp>
        {console.log(data)}
        {data.name && (
          <h1>
            {data.name}, {data.sys.country}
          </h1>
        )}
        <div className="temperature">
          <img
            src={`http://openweathermap.org/img/wn/${data.weather[0].icon}@2x.png`}
            alt="sunny"
          />
          <h2>{String(data.main.temp).slice(0, 4)}&deg;C</h2>
        </div>
        <h2>{data.weather[0].description}</h2>
      </CurrentTemp>
      <WeatherDetails>
        <h2>
          {currentDate} <span>{currentHour}</span>
        </h2>
        <CurrentDetails data={data} />
      </WeatherDetails>
    </Container>
  ) : (
    <CityNotFound>
      {data.cod === "400" ? (
        <h1>Check the weather for chosen city</h1>
      ) : (
        <h1>City Not Found</h1>
      )}
    </CityNotFound>
  );
};

export default CityWeather;
