import React from "react";
import styled from "styled-components";

import video_day from "../assets/sun.mp4";
import video_night from "../assets/night.mp4";
import video_rain from "../assets/rain.mp4";

import { appear } from "../styles/Animations";

const Video = styled.video`
  animation: ${appear} 1s ease-in;
  width: 100vw;
  height: 100vh;
  -o-object-fit: cover;
  object-fit: cover;
  z-index: -1;
`;

const VideoBg = (props) => {
  const { data } = props;
  let video_src = video_day;
  if (data.cod === 200) {
    const sunset = data.sys.sunset;
    const sunrise = data.sys.sunrise;
    const current_time = data.dt;
    if (data.weather[0].main === "Rain") video_src = video_rain;
    else if (current_time < sunrise || current_time > sunset)
      video_src = video_night;
  }

  return <Video autoPlay loop muted src={video_src} type="video/mp4" />;
};

export default VideoBg;
