import React from "react";
import styled from "styled-components";

import DailyForecast from "./DailyForecast";
import { device } from "../res/Device";

const Container = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 3rem;
  left: 50%;
  transform: translateX(-50%);
  width: 70vw;
  height: 16rem;
`;

const Forecast = (props) => {
  const { data } = props;
  console.log(data);

  return (
    <Container>
      <DailyForecast data={data.daily[0]} timezone={data.timezone_offset} />
      <DailyForecast data={data.daily[1]} timezone={data.timezone_offset} />
      <DailyForecast data={data.daily[2]} timezone={data.timezone_offset} />
      <DailyForecast data={data.daily[3]} timezone={data.timezone_offset} />
      <DailyForecast data={data.daily[4]} timezone={data.timezone_offset} />
    </Container>
  );
};

export default Forecast;
