import styled from "styled-components";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin: 1rem;
  width: 8rem;
  height: 5rem;
  text-transform: uppercase;
  & p {
    font-weight: 100;
    letter-spacing: 1px;
  }
  /* background-color: #fff; */
`;

const Value = (props) => {
  const { data, name } = props;
  if (name === "pressure") {
    return `${data.main.pressure} kPa`;
  } else if (name === "sunrise") {
    return `${new Date(
      data.sys.sunrise * 1000 + data.timezone * 1000
    ).toLocaleTimeString([], {
      hour: "2-digit",
      minute: "2-digit",
    })}`;
  } else if (name === "sunset") {
    return `${new Date(
      data.sys.sunset * 1000 + data.timezone * 1000
    ).toLocaleTimeString([], {
      hour: "2-digit",
      minute: "2-digit",
    })}`;
  } else if (name === "wind") {
    return `${data.wind.speed} m/s`;
  } else if (name === "humidity") {
    return `${data.main.humidity}%`;
  } else if (name === "cloudiness") {
    return `${data.clouds.all}%`;
  } else return `~`;
};

const Detail = (props) => {
  const { data, name } = props;
  return (
    <Container>
      <h2 className="detailValue">
        <Value data={data} name={name} />
      </h2>
      <p className="detailLabel">{name}</p>
    </Container>
  );
};

export default Detail;
