import styled from "styled-components";
import React from "react";

const Input = styled.input`
  font-family: inherit;
  font-size: inherit;
  margin-top: 5rem;
  padding: 0.25rem 0.5rem;
  width: 40rem;
  height: 2rem;
  line-height: 1rem;
  border: none;
  background-color: transparent;
  border-bottom: 2px solid #d2e69c;
  &::placeholder {
    color: rgb(0, 0, 0, 0.5);
  }

  transition: all 0.2s ease;

  &:hover {
    cursor: pointer;
  }

  &:focus {
    outline: none;
    transform: scale(1.05);
    padding-bottom: 1rem;
    font-size: 1.5rem;
    text-align: left;
  }

  /* mix-blend-mode: multiply; */
`;

const SearchBar = (props) => {
  return (
    <Input
      type="text"
      placeholder="Enter the city"
      className="container"
      value={props.value}
      onChange={props.onChange}
    ></Input>
  );
};

export default SearchBar;
